body,
html {
	margin: 0;
	padding: 0;
	font-family: "Open Sans", sans-serif;
	color: #fff;
	overflow: scroll;
	overflow-x: hidden;
}

::-webkit-scrollbar {
	width: 0;
	background: transparent;
}

body {
	background-color: rgb(10, 14, 22);
}

h1 {
	font-size: 3.2em;
	line-height: 1.1;
}

#canvas {
	width: 100%;
	height: 70%;
	position: absolute;
	bottom: 0;
	left: 0;
	touch-action: manipulation;
}

main {
	position: relative;
}

header {
	margin-top: 3em;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
}

header > img {
	width: 13em;
	height: 13em;
	border-radius: 100%;
	border: 1px solid #fff;
	box-shadow: rgb(28 32 93 / 24%) 0px 2px 8px 0px;
}

#canva {
	width: 100%;
	height: 60%;
	position: absolute;
	bottom: 0;
	left: 0;
	z-index: 0;
}

header > h1 {
	display: inline-block;
	font-size: 0.8em;
	font-weight: bold;
	border-radius: 0.4em;
	background-color: #000a;
	color: #fff;
	padding: 0.5em 1em;
	border: 1px solid rgba(255, 255, 255, 0.336);
	backdrop-filter: blur(10px) saturate(160%) contrast(180%);
	-webkit-backdrop-filter: blur(10px) saturate(160%) contrast(180%);
}

ul {
	box-sizing: border-box;
	list-style: none;
	margin: 0 auto;
	padding: 2em;
	max-width: 500px;
	display: flex;
	flex-wrap: wrap;
	gap: 0.4rem;
	justify-content: center;
}

ul > li {
	border: 1px rgba(255, 255, 255, 0.288) solid;
	backdrop-filter: blur(4px) saturate(110%) contrast(110%);
	-webkit-backdrop-filter: blur(4px) saturate(110%) contrast(110%);
	border-radius: 0.5em;
	margin: 0.2em 0;
	min-width: 10rem;
	display: inline-block;
}

ul > li > a {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	gap: 0.5em;
	padding: 0.8rem 1em;
}

a {
	color: rgba(255, 255, 255, 0.767);
	text-decoration: none;
	transition: transform 100ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

a:hover {
	color: rgba(255, 255, 255);
	transform: scale(1.04);
}

.btn::before,
.btn::after {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	bottom: 0;
	left: 0;
	z-index: 1;
	transition: all 0.3s;
	border: 1px solid rgba(255, 255, 255, 0.5);
}

.btn:hover::after {
	animation-name: rotatecw;
	animation-duration: 2s;
}
.btn:hover::before {
	animation-name: rotateccw;
	animation-duration: 3s;
}
.btn:hover::after,
.btn:hover::before {
	/* left: 96px; */
	/* width: 64px; */

	animation-iteration-count: infinite;
	animation-timing-function: linear;
}

@keyframes rotatecw {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@keyframes rotateccw {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(-360deg);
	}
}
